function QuoteText(props) {
  return(
      <div id="text"><i class="fa fa-quote-left"></i>{props.text}</div>
  )
}

function QuoteAuthor(props) {  
  return(
      <div id="author">- {props.author}</div>
  );
}

// /* <button style='font-size:24px'>Button <i class='fab fa-twitter'></i></button> */
function TweetQuote() {
  return(
      <div><a href="http://twitter.com/intent/tweet" id="tweet-quote"><i className='fab fa-twitter'></i> Tweet</a></div>

  )
}

class NewQuote extends React.Component {
  // constructor(props) {
  //   super(props);
  //   // this.onClick = this.onClick.bind(this);
  // }

  render() {
    return (
        <div id="new-quote" onClick= {this.props.onClick}>
          New Quote
        </div>
    )
  }
}

class QuoteBox extends React.Component {
  constructor(props) {
    super(props);

    this.quotes = this.props.quotes;
    const quote = this.randomQuote();
    this.state = {
      text: quote.text,
      author: quote.author
    }
    // this.handleClick();
    // this.handleClick = this.handleClick.bind(this);
  }

  componentDidMount(){
    this.handleClick();
    this.handleClick = this.handleClick.bind(this);
  }

  // 続けて同じモノが出される場合がある
  randomQuote() {
    return this.quotes[Math.floor(Math.random() * this.quotes.length)];
    // const quotes = this.props.quotes;
    // return quotes[Math.floor(Math.random() * quotes.length)];
  }

  handleClick() {
    // alert(123);
    const quote = this.randomQuote();
    this.setState({
      text: quote.text,
      author: quote.author
    });
  }

  render() {
    // console.log(this.randomQuote());
    // const quote = this.randomQuote();
    // console.log(quote.text, quote.author)
    let boxStyle = {
      textAlign: 'center'
    };


    return (
      <div id="quote-box" style={boxStyle}>
        <QuoteText text={this.state.text} />
        <QuoteAuthor author={this.state.author} />
        <TweetQuote />
        <NewQuote onClick={this.handleClick} />
      </div>
    );
  }
}

function App() {
  return (
      <QuoteBox quotes={QUOTES} />
  );
}

